/**
 * 
 * @param {HTMLElement} target 
 * @param {Array} items 
 * @param {Object} config 
 * @returns 
 */
function contextMenu(items, config = {}) {
    var container = document.createElement('div');
    var createdContainer = false;
    var items = items;
    var listeners = {};
    container.className = 'winui-contextmenu-container';

    function triggerEvent(event, detail) {
        if (listeners[event]) {
            listeners[event].forEach(listener => listener(detail));
        }
    }

    function generateMenu(target, items, config) {
        var menuLayer = document.createElement('div');
        var menuList = document.createElement('div');
        menuLayer.className = 'winui-contextmenu-menu-layer';
        menuList.className = 'winui-contextmenu-menu';
        target.appendChild(menuLayer);
        menuLayer.appendChild(menuList);

        items.forEach(item => {
            var itemElement = document.createElement('div');
            itemElement.className = 'winui-contextmenu-menu-item';
            menuList.appendChild(itemElement);

            if (item.type == 'separator') {
                return itemElement.className = 'winui-contextmenu-menu-separator';
            }

            var itemIcon = document.createElement('div');
            var itemText = document.createElement('div');
            var itemExpand = document.createElement('div');
            var itemSubmenu = document.createElement('div');
            var submenu = null;
            var created = false;
            var focused = false;
            var pointerOver = false;
            var disabled = item.disabled == true;

            itemIcon.className = 'winui-contextmenu-menu-icon';
            itemText.className = 'winui-contextmenu-menu-text';
            itemExpand.className = 'winui-contextmenu-menu-expand';
            itemSubmenu.className = 'winui-contextmenu-submenu';

            itemIcon.innerHTML = icons[item.icon] ? '&#x' + icons[item.icon] + ';' : '';
            itemText.innerText = item.text;

            if (!icons[item.icon] && item.icon) {
                itemIcon.style.backgroundImage = 'url(' + item.icon + ')';
                itemIcon.style.backgroundSize = 'cover';
                itemIcon.style.backgroundPosition = 'center';
                itemIcon.style.backgroundRepeat = 'no-repeat';
            }

            itemElement.appendChild(itemIcon);
            itemElement.appendChild(itemText);
            itemElement.appendChild(itemExpand);
            itemElement.appendChild(itemSubmenu);

            if (!item.submenu || item.type == 'label') {
                itemExpand.remove();
            }
            if (config.showIcon == false || item.type == 'label') {
                itemIcon.remove();
            }
            if (disabled == true) {
                itemElement.classList.add('disabled');
            }
            if (item.type == 'label') {
                itemElement.className = 'winui-contextmenu-menu-label';
            }
            if (item.selected == true) {
                itemElement.classList.add('selected');
            }

            itemElement.addEventListener('pointerover', () => {
                if (disabled == true || item.type == 'label') return;
                pointerOver = true;
                if (created == true) return;
                if (item.submenu) {
                    itemElement.classList.add('active');
                    submenu = generateMenu(itemSubmenu, item.submenu, config);
                    created = true;
                }
            })

            itemElement.addEventListener('pointerout', () => {
                if (disabled == true || item.type == 'label') return;
                pointerOver = false;
                function removeSubmenu() {
                    if (pointerOver == true || focused == true || submenu == null) return clearTimeout(removeSubmenu);
                    submenu.remove();
                    submenu = null;
                    created = false;
                    itemElement.classList.remove('active');
                    clearTimeout(removeSubmenu);
                }
                setTimeout(removeSubmenu, 200)
            })

            itemElement.addEventListener('click', () => {
                if (disabled == true || item.type == 'label') return;
                focused = true;
                if (created == true) return;
                if (item.submenu) {
                    itemElement.classList.add('active');
                    submenu = generateMenu(itemSubmenu, item.submenu, config);
                    created = true;
                } else {
                    item.action();
                    triggerEvent('select', {
                        
                    })
                    close();
                }
            });
            return;
        });
        return menuLayer;
    }

    function open(horizontal, vertical, fixed = 'left-top') {
        if (fixed.includes('right')) {
            container.style.right = horizontal + 'px';
        } else {
            container.style.left = horizontal + 'px';
        }
        if (fixed.includes('bottom')) {
            container.style.bottom = vertical + 'px';
        } else {
            container.style.top = vertical + 'px';
        }
        container.style.zIndex = '1';
        if (createdContainer == false) {
            document.body.appendChild(container);
            generateMenu(container, items, config);
        }
        createdContainer = true;
    }

    function close() {
        createdContainer = false;
        container.remove();
        container.innerHTML = '';
        container.style.zIndex = '-1';
    }

    function setItems(value) {
        items = value;
    }

    function on(event, listener) {
        if (!listeners[event]) {
            listeners[event] = [];
        }
        listeners[event].push(listener);
    }

    return { open, close, setItems, on, container }
}

export default contextMenu;